import ThirdShare from '@/components/Common/ThirdShare'
import config from "@/config/common.config"
import {scientificService, paperService} from '@/config/mock'
export default {
  components:{
    ThirdShare
  },
  props:{
    headerConfig:{
      type:Object,
      required:true
    }
  },
  data() {
    return {
      headerActive:this.$route.fullPath === '/biunique/scientific' ? 1 : 2,
      service:1,
      serviceShow:false,
      config
    }
  },
  computed:{
    severList() {
      let obj = this.headerConfig.key==='scientific' ? scientificService : paperService
      return Object.keys(obj).map((key)=>({title:obj[key], value:+key}))
    },
    bannerSrc() {
      return  this.headerConfig.poster ? `${config.IMG_URL.default}${this.headerConfig.poster}` : `https://static.ais.cn/resource/meeting/photo/202011/${this.headerConfig.key==='paper' ? '353201119134110010':'302201118140744027'}.png`
    },
    shareOption() {
      return {
        title : this.headerConfig.title,
        desc: `我已参加${this.headerConfig.title}，你也来一起参加吧!`,
        imgUrl: this.bannerSrc,
        link: `${config.LINK.school_h5}${this.$route.fullPath}`
      }
    }
  },
  methods:{
    headerClick(val){
      this.headerActive = val
      this.$router.push(val === 1 ? '/biunique/scientific' :'/biunique/paper')
    },
    //打开分享窗口
    openShare(){
      this.$refs.ThirdShare.open()
    },
    goApply() {
      this.serviceShow = true
    },
    toApply() {
      this.$router.push( `/biunique/${this.headerConfig.key==='scientific' ? 'scientificApply' :'paperApply'}/${this.service}?categoryId=${this.headerConfig.id || 0}`)
    }
  }
}
