import BiuniqueBanner from '@/components/biunique/BiuniqueBanner'
import { getCategoryInfo } from '@/api/biunique'
import isFirstShare from '@/mixins/isFirstShare'
export default {
  components:{
    BiuniqueBanner
  },
  mixins:[isFirstShare],
  async asyncData (context) {
    const { data } = await getCategoryInfo(context.route.params.id)
    if (data.code === 0) {
      return {
        data: {
          ...data.data.entries,
          title:data.data.entries.name,
          btnTitle:'申请匹配',
          des:data.data.entries.introduce,
          key:'scientific'
        }
      }
    }
  },
  head () {
    return {
      title: '科研背景提升'
    }
  }
}
